.canvas-col {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  margin-top: 2em; }

table {
  @include border;
  background: #fff;
  border-collapse: collapse;
  margin: 2em auto;
  transition: all 400ms ease-in-out;
  cursor: pointer;

  tr {
    @include border;
    height: 1.25em; }

  td {
    @include border;
    width: 1.25em; } }

// Animations

.fadeIn {
  animation: fadeIn 1s ease-in-out; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

// Footer

.footer {
  flex-shrink: 0;
  padding-bottom: 1.25em;
  text-align: center;

  .footer-link {
    text-decoration: none;
    color: #333;
    font-weight: 700;
    letter-spacing: 1px;
    transition: color 400ms ease-in-out;
    &:hover {
      color: #fff; } } }

// Media queries

@media screen and (max-width: 47em) {
  table {
    margin: 2em 1em; } }
